<template>
    <div>
        <TopNav title="添加砍价"></TopNav>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <div class="wyu-tips wyu-tips-info" style="margin-bottom:10px;">
                    <p class="tit">1)砍价产品包邮以 关联产品为准</p>
                    <p class="tit">2)砍价活动 一旦有人开启 则不可以关闭 与 修改 请仔细填写 停掉所有砍价后才可以 删除关闭修改 等操作</p>
                    <p class="tit">3)如果商品多个规格 在砍价时 统一为 初始价 </p>
                </div>
                <el-form v-if="active == 1"  :model="goodsDetail" ref="goodsInfo" size="mini"
                         label-width="100px">
                    <div class="wyu-title">
                        <h2>选择产品</h2>
                    </div>
                    <el-form-item label="选择产品" >
                        <product-select-spec @selGoods="getGoods" :prom_type='0'></product-select-spec>
                    </el-form-item>
                    <el-form-item label="产品名称 " v-if="goods">
                            {{goods.goods_name  }}
                    </el-form-item>
                    <el-form-item label="产品图片" v-if="goods">
                        <img :src="goods.photo" width="100"/>
                    </el-form-item>
                    <el-form-item label="选择规格"  v-if="goods">
                        <div v-if="goods.sku_type == 0">
                            价格：{{goods.shop_price}}
                        </div>
                        <template v-if="goods.sku_type == 1">
                            <el-table size="mini"  row-key="spec_key" @selection-change="seleSpec"    ref="multipleTable" :data="getGoodsData()"  height="250"  border style="width: 100%">
                                <el-table-column :reserve-selection="true" prop="spec_key" type="selection" width="55"></el-table-column>
                                <el-table-column prop="key_name" label="规格项"></el-table-column>
                                <el-table-column prop="shop_price" label="价格"></el-table-column>
                                <el-table-column prop="goods_num" label="库存"></el-table-column>
                            </el-table>
                        </template>
                    </el-form-item>
                    <div class="wyu-title">
                        <h2>基本信息</h2>
                    </div>
                    <el-form-item label="初始价格">
                        <el-input v-model="goodsDetail.init_price" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">砍价初始的价格</p>
                    </el-form-item>
                    <el-form-item label="砍价底价">
                        <el-input v-model="goodsDetail.bargain_price" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">需要砍到多少才可以购买</p>
                    </el-form-item>
                    <el-form-item label="需要人数">
                        <el-input v-model="goodsDetail.need_num" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">指定多少人数才能砍完 请合理设置 最大数（初始化价格 * 100/2） 即上下浮动 0.01 元 默认 50-100 人</p>
                    </el-form-item>
                    <el-form-item label="砍价有效期">
                        <el-input v-model="goodsDetail.end_time" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">参与砍价后 多少小时后未看到底价 即为砍价失败 单位（小时）</p>
                    </el-form-item>
                    <el-form-item label="排序">
                        <el-input v-model="goodsDetail.orderby" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">砍价售出数量限制 不得大于原始库存 如原始 库存为 0 则此处 也为零</p>
                    </el-form-item>
                    <el-form-item label="已成功砍价人数">
                        <el-input v-model="goodsDetail.success_num" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">已成功砍价人数</p>
                    </el-form-item>
                    <el-form-item label="是否开启">
                        <el-switch v-model="goodsDetail.is_online" :active-value="1" :inactive-value="0"></el-switch>
                    </el-form-item>
                    <el-form-item label="VIP专属">
                        <el-switch v-model="goodsDetail.is_vip_more_bargain" :active-value="1" :inactive-value="0"></el-switch>
                        <p class="wyu-form-tips">plus 会员可以多砍一刀 并且 帮砍用户 也是VIP 也同理 多砍一刀</p>
                    </el-form-item>


                    <div class="wyu-title">
                        <h2>SEO设置</h2>
                    </div>
                    <el-form-item label="百度SEO标题" label-width="120px">
                        <el-input v-model="goodsDetail.seo.title" style="width:500px;"  autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO描述" label-width="120px">
                        <el-input type="textarea" :autosize="{minRows: 2, maxRows: 5}" style="width:500px;" autocomplete="off"  placeholder="请输入内容" v-model="goodsDetail.seo.keywords"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO关键词" label-width="120px">
                        <el-input type="textarea" :autosize="{minRows: 5, maxRows: 10}" style="width:500px;" autocomplete="off"  placeholder="请输入内容" v-model="goodsDetail.seo.description"></el-input>
                    </el-form-item>
                    <el-form-item label="百度SEO原始发布时间" label-width="120px">
                        <el-date-picker v-model="goodsDetail.seo.releaseDate" type="date" placeholder="选择日期"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="百度封面图片" label-width="120px">
                        <img-uploads :multi="true" :img="goodsDetail.seo.image"  v-model="goodsDetail.seo.image"></img-uploads>
                        <p class="tit"> 用于信息流投放后的封面显示，最多3张，单图片最大2M；封面图建议尺寸：高>=210px 宽>=375px;最小尺寸: 高>=146px 宽>=218px</p>
                    </el-form-item>

                </el-form>
            </div>
        </div>
        <div class="wyu-btn-div">
            <el-button size="small" type="primary"  @click="createGoods()">创建砍价</el-button>
            <router-link to="/marketing/bargain/index" style="margin-left: 10px">
                <el-button size="small">取消</el-button>
            </router-link>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import ProductSelectSpec from "../../product/components/ProductSelectSpec";
    import ImgUploads from "../../../../../sqtuan/src/components/Photos";
    export default {
        name: 'BargainCreate',
        components: {
            ImgUploads,
            ProductSelectSpec,
            TopNav
        },
        data() {
            return {
                loading: false,
                active: 1,
                typeId: 0,
                goods: {},
                goodsDetail: {
                    goods_id:0,
                    spec:[],
                    num:0,
                    bargain_price:0,
                    init_price:0,
                    is_online:1,
                    end_time:0,
                    orderby:0,
                    is_vip_more_bargain:1,
                    success_num:0,
                    need_num:0,
                    seo: {
                        title: '',
                        keywords: '',
                        description: '',
                        releaseDate: '',
                        image: [],
                    },
                },

            }
        },
        created() {

        },

        methods: {

            getGoodsData(){
                let data = [];
                this.goods.goods_spec.forEach(v => {
                    if(v.goods_num > 0){
                        data.push(v);
                    }
                });
                return data;
            },

            getGoods(val) {
                this.goods = val;
                if(this.$refs.multipleTable != undefined) {
                    console.log(this.$refs.multipleTable);
                    this.$refs.multipleTable.clearSelection();
                }
                this.goodsDetail.spec = val.goods_spec;
            },



            seleSpec(val){
                let spec_key = [];
                val.forEach( v => {
                    spec_key.push(v.spec_key);
                });
                this.goodsDetail.spec = spec_key;
            },
            //提交商品到商品库
            createGoods() {
                this.loading = true;
                api.post("/shop/admin.marketing.bargain/create", {
                    appToken: this.$store.getters.appToken,
                    goods_id:this.goods.goods_id,
                    spec: JSON.stringify(this.goodsDetail.spec),
                    num:this.goodsDetail.num,
                    bargain_price:this.goodsDetail.bargain_price,
                    init_price:this.goodsDetail.init_price,
                    is_online:this.goodsDetail.is_online,
                    end_time:this.goodsDetail.end_time,
                    orderby:this.goodsDetail.orderby,
                    is_vip_more_bargain:this.goodsDetail.is_vip_more_bargain,
                    success_num:this.goodsDetail.success_num,
                    need_num:this.goodsDetail.need_num,
                    seo:JSON.stringify(this.goodsDetail.seo),
                }).then(res => {
                    this.loading = false;
                    this.$message({
                        message: '提交成功',
                        type: 'success'
                    });
                    setTimeout(() => {
                        this.$router.push("/marketing/bargain/index");
                    }, 1000);
                }).catch((error) => {
                    this.loading = false;
                });
            },
        }
    }
</script>
